.greet{
    padding:0px;
}

h1{
font-size: 25px;
transition-duration: 0.5s;
animation-name: example;
animation-duration: 1s;
animation-fill-mode: forwards;
}



.dot{
    color: crimson;
    transition-duration: 0.5s;
}

@keyframes example {
    from{font-size: 25px;}
    to{font-size: 105px;}
}

@media(max-width:784px){
    @keyframes example {
        from{font-size: 25px;}
        to{font-size: 70px;}
    }
}

.container-main{
    padding: 100px;
    height: 675px;
    background-color: black;
    color: blanchedalmond;
}

/* Carousel*/

.crossfade > figure {
    animation: imageAnimation 30s linear infinite 0s;
    backface-visibility: hidden;
    background-size: cover;
    background-position: center center;
    color: transparent;
    height: 100%;
    left: 0px;
    opacity: 0;
    position: absolute;
    top: 0px;
    width: 100%;
    z-index: 0;
  }


  .crossfade > figure:nth-child(1) { background-image: url('../Images/1.jpg'); }

.crossfade > figure:nth-child(2) {
  animation-delay: 6s;
  background-image: url('../Images/2.jpg');
}

.crossfade > figure:nth-child(3) {
  animation-delay: 12s;
  background-image: url('../Images/3.png');
}

.crossfade > figure:nth-child(4) {
  animation-delay: 18s;
  background-image: url('../Images/4.png');
}

.crossfade > figure:nth-child(5) {
  animation-delay: 24s;
  background-image: url('../Images/5.jpg');
}

.crossfade >figure:nth-child(6){
  animation-delay: 30s;
  background-image: url('../Images/6.jpg');
}

.crossfade >figure:nth-child(7){
  animation-delay: 36s;
  background-image: url('../Images/i\ love\ coding\ wallpaper.jpg');
}

.crossfade >figure:nth-child(8){
  animation-delay: 42s;
  background-image: url('../Images/Light\ wallpaper.jpg');
}

.crossfade >figure:nth-child(9){
  animation-delay: 48s;
  background-image: url('../Images/wallpapersden.com_superman-amoled_3840x2160.jpg');
}


@keyframes 
imageAnimation {  0% {
 animation-timing-function: ease-in;
 opacity: 0;
}
 8% {
 animation-timing-function: ease-out;
 opacity: 0.5;
}
 17% {
 opacity: 0.5
}
 25% {
 opacity: 0
}
 100% {
 opacity: 0
}
}

/*Carousel end*/